import React, { useMemo, useEffect, useState } from "react";
import { Elements, CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import { loadStripe, Stripe, StripeElements } from "@stripe/stripe-js";
import { Card, Typography, Button } from "@material-ui/core";
import API from "../../Services/API";

var stripePromise;
const useOptions = () => {
    const options = useMemo(() => ({
        style: {
            base: {
                color: "#424770",
                letterSpacing: "0.025em",
                fontFamily: "Source Code Pro, monospace",
                "::placeholder": {
                    color: "#aab7c4"
                }
            },
            invalid: {
                color: "#9e2146"
            }
        }
    }), ["16px"]);

    return options;
};

export function StripeCard(props) {
    const [clientSecret, setClientSecret] = useState('');
    const [status, setStatus] = useState("");
    const [disabled, setDiabled] = useState(true);
    const [error, setError] = useState('');
    const stripe = useStripe() as Stripe;
    const elements = useElements() as StripeElements;

    useEffect(() => {
        API.GetStripeSecret(props.amount)
            .then(data => {
                setClientSecret(data.clientSecret);
                console.info("Client Secret", data.clientSecret);
            }).catch(ex => setError(ex.message));
    }, []);

    async function onSubmit(event) {
        event.preventDefault();
        event.stopPropagation();
        if (status !== "") return;
        setStatus("LOADING");
        try {
            const payload = await stripe.confirmCardPayment(clientSecret, {
                payment_method: { card: elements.getElement(CardElement) as any }
            });
            if (payload.error) throw payload.error;
            setStatus("OK");
            props.onDone(payload.paymentIntent);
        } catch (ex) {
            setStatus("");
            setError(ex.message);
            console.warn("Stripe Error", ex);
        }
    }
    function onChange(event) {
        setDiabled(event.empty);
        setError((event.error && event.error.message) || "");
    }

    const options = useOptions();

    if (!clientSecret) {
        return <Typography color={error ? "secondary" : "initial"} style={{ textAlign: "center", margin: "auto" }} variant="button">
            {error ? error : "Cargando..."}
        </Typography>
    }

    return (<form onSubmit={onSubmit}>
        <Card elevation={6} style={{ margin: "auto", textAlign: "center", maxWidth: 400, padding: 10 }}>
            <div style={{ padding: 10 }}>
                <CardElement options={options}
                    onChange={onChange}
                />
            </div>

            {error && <Typography color="secondary">{error}</Typography>}

            <Button fullWidth variant="contained" color="primary" type="submit"
                disabled={disabled || !!status} >
                {status === "LOADING"
                    ? "CARGANDO..."
                    : (status === "OK"
                        ? "LISTO!"
                        : "PAGAR")}
            </Button>
        </Card>
    </form >)
}

export default function (props) {
    const { API_KEY, ...p } = props;
    if (!API_KEY) return (<p>Stripe no est configurado en esta tienda. Intenta otro mtodo de pago</p>);
    if (!stripePromise) stripePromise = loadStripe(API_KEY);
    return (<Elements stripe={stripePromise}>
        <StripeCard {...p} />
    </Elements>);
}